.section{
  &-objects{
    margin-top: 76px;
  }
  &-catalog{
    &__wrapper{
      padding: 40px 0 80px 0;
    }
    &__block + &__block{
      margin-top: 30px;
    }
  }
  &-services{
    margin-top: 50px;
  }
}


@media screen and (min-width: 991px) {
  .section{
    &-catalog{
      &__wrapper{
        padding: 80px 0 130px 0;
      }
      &__block + &__block{
        margin-top: 70px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-services{
      .container{
        padding-right: 0;
      }
    }
  }
}

@media screen and (max-width: 500px){
  .section{
    &-services{
      display: none;
    }
  }
}