body {
  font-family: 'Graphik', sans-serif;
  font-weight: 400;
  color: #000;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.page-wrapper{
  flex: 1 0 auto;
}

.page-item{
  padding-top: 127px;
  &__content{
    padding-top: 30px;
    background: #F4F4F4;
  }
}

.container {
  max-width: 1320px;
  width: 100%;

  margin: 0 auto;

  padding: 0 20px;
}


@media screen and (min-width: 600px) {
  .container {
    padding: 0 60px;
  }
}

.catalog {
  &-block {
    &__title {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__label {
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
      font-size: 32px;
      color: #151515;

      svg {
        width: 30px;
        height: 30px;
        margin-right: 20px;
      }
    }

    &__link {
      font-size: 18px;
      font-weight: 500;
      color: #FF4D35;
    }

    &__container {
      margin-top: 25px;
    }
  }
}

.btn {
  -webkit-appearance: none;

  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    cursor: pointer;
  }

  &-orange {
    background-color: #FF4D35;
    border-radius: 4px;
    font-size: 13px;
    color: #Fff;
    font-weight: 500;
  }
}

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  &__item {
    width: 100%;
  }
}

.catalog {
  &-burger {
    width: 14px;
    height: 12px;
    margin-right: 26px;

    .burger {
      &__item {
        height: 2px;
        background-color: #fff;
        border-radius: 30px;
      }
    }

    &_black {
      .burger {
        &__item {
          background-color: #000;
        }
      }
    }

  }
}

.page-wrapper {
  padding-top: 150px;
}

.breadcrumb{
  &__list{
    display: flex;
    align-items: center;
  }
  &-list{
    &__item + &__item{
      &::before{
        content: '/';
        margin: 0 10px;
        color:#828589;
      }
    }
    &__item:last-child{
      pointer-events: none;
    }
  }
  &-item{
    &__link{
      font-size: 13px;
      color:#828589;
    }
  }
}

@media screen and (max-width: 991px) {
  .catalog {
    &-block {
      &__label {
        font-size: 24px;
      }

      &__link {
        font-size: 14px;
      }

      &__container {
        padding-right: 0;
      }
    }
  }
}


.item-img-slider {
  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background-color: #ffffff;
    opacity: .4;

    &-active {
      opacity: 1;
    }
  }
}

.radio-input {
  &__item {
    display: none;
  }
}

.text-input {
  -webkit-appearance: none;
  width: 100%;
  height: 55px;

  border: 1px solid #D2D8DE;
  border-radius: 7px;

  padding: 0 18px;
  font-size: 18px;

  outline: none;
}

@media screen and (max-width: 991px) {
  .text-input {
    font-size: 16px;
  }
  .page-item{
    padding-top: 54px;
  }
}

@media screen and (max-width: 450px) {
  .page-wrapper{
    padding-top: 80px;
  }

}